/* stylelint-disable length-zero-no-unit */
/* stylelint-disable rule-empty-line-before */
.calendar {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  width: 726px;
  max-height: 625px;
  padding: 10px;
  gap: 1px;
}
.calendar__day {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 1 solid #000;
  background-color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: all 0.5s;
}
.calendar__day:hover {
  cursor: pointer;
  background-color: #FFBFCB;
  transform: translate(0, -20px);
}
.calendar__day:nth-child(1)::before {
  content: "1";
}
